  position: relative;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #7dd3fc;
  animation: dotFlashing 1s infinite linear alternate;
  animation-delay: 0.5s;
}

.dot-flashing-small::before,
.dot-flashing-small::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
}

.dot-flashing-small::before {
  left: -10px;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #7dd3fc;
  animation: dotFlashing 1s infinite alternate;
  animation-delay: 0s;
}

.dot-flashing-small::after {
  left: 10px;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #7dd3fc;
  animation: dotFlashing 1s infinite alternate;
  animation-delay: 1s;
}
